@forward '../markdown/partial-styles/_kbd.scss';
:host(limel-hotkey) {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.25rem;
}

kbd {
    margin: 0;
    font-size: 0.75rem;
    box-shadow:
        var(--button-shadow-pressed),
        0 0.625rem 0.375px -0.5rem rgb(var(--color-black), 0.02),
        0 0.025rem 0.5rem 0 rgb(var(--contrast-100)) inset;
}
